body {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
  	margin: 0;
  	padding: 0;
  	font-family: sans-serif;
}

.header-settings {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	min-height: 100px;
	width: 100%;
	background-color: black;
	color: #2196f3;
	margin-top: 0px;
	text-align: center;
}

.centering {
	text-align: center;
}

.centering-check-box {
	display: -ms-flexbox;
	display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
	text-align: center;
}

.offset {
	margin: 0 auto;
}

.reset {
	padding: 0px;
	padding-left: 15px;
}

.input-space {
	margin-top: 2%;
}

.offset-complete-icon {
	margin-right: 1%;
}

.complete {
	color: lightgreen;
}

.complete:hover {
	color: #c0f3c0;
}

.delete {
	color: red;
	background-color: white;
}

.delete:hover {
	color: #e38e8e;
}

.resize {
	width: 80%;
}

.resize-container {
	text-align: center;
	width: 20%;
	z-index: 50;
	background-color: white;
	height: 100%;
	border-top-right-radius: .25rem;
    border-top-left-radius: .25rem;
}

.resize-container:hover {
	cursor: pointer;
}

@media only screen and (max-width: 500px) {
	#root {
		padding: 3%;
	}
	header > h1 {
		font-size: 1.5em;
	}
}  


